        source: "import {\n  DxPluginHost, DxPlugin, DxGetter, DxAction,\n  DxTemplate, DxTemplatePlaceholder, DxTemplateConnector,\n} from '@devexpress/dx-vue-core';\n\nexport default {\n  data() {\n    return {\n      tasks: [\n        { title: 'call mom', done: false },\n        { title: 'send letters to partners', done: false },\n        { title: 'buy milk', done: true },\n        { title: 'rent a car', done: false },\n      ],\n    };\n  },\n  render() {\n    return (\n      <TasksList tasks={this.tasks}>\n        <TasksFilter defaultDone={false} />\n        <FilterPanel />\n      </TasksList>\n    );\n  },\n};\n\nconst TasksList = {\n  render() {\n    return (\n      <DxPluginHost>\n        <TasksListCore {...{ attrs: this.$attrs }} />\n        {this.$slots.default}\n      </DxPluginHost>\n    );\n  },\n};\n\nconst TasksListCore = {\n  props: {\n    tasks: {\n      type: Array,\n    },\n  },\n  render() {\n    return (\n      <DxPlugin>\n        <DxGetter name=\"tasks\" value={this.tasks} />\n        <DxTemplate name=\"root\">\n          <div>\n            <DxTemplatePlaceholder name=\"header\" />\n            <DxTemplateConnector>\n              {({ getters: { tasks } }) => (\n                <ul>\n                  {tasks.map(({ title, done }, index) => (\n                    <li\n                      key={index}\n                      style={{ textDecoration: done ? 'line-through' : '' }}\n                    >\n                      {title}\n                    </li>\n                  ))}\n                </ul>\n              )}\n            </DxTemplateConnector>\n          </div>\n        </DxTemplate>\n      </DxPlugin>\n    );\n  },\n};\n\nconst TasksFilter = {\n  props: {\n    defaultDone: {\n      type: Boolean,\n    },\n  },\n  data() {\n    return {\n      done: this.defaultDone,\n    };\n  },\n  methods: {\n    changeFilter(done) {\n      this.done = done === undefined ? !this.done : done;\n    },\n  },\n  render() {\n    return (\n      <DxPlugin>\n        <DxGetter\n          name=\"tasks\"\n          computed={({\n            tasks,\n          }) => tasks.filter(task => this.done === null || task.done === this.done)}\n        />\n        <DxGetter name=\"filter\" value={this.done} />\n        <DxAction name=\"changeFilter\" action={this.changeFilter} />\n      </DxPlugin>\n    );\n  },\n};\n\nconst FilterPanel = {\n  render() {\n    return (\n      <DxPlugin>\n        <DxTemplate name=\"header\">\n          <DxTemplateConnector>\n            {({ getters: { filter }, actions: { changeFilter } }) => (\n              <div>\n                Filter: ({JSON.stringify(filter)})\n                {' '}\n                <button onClick={() => changeFilter()}>Change</button>\n                {' '}\n                <button onClick={() => changeFilter(null)}>Clear</button>\n              </div>\n            )}\n          </DxTemplateConnector>\n        </DxTemplate>\n      </DxPlugin>\n    );\n  },\n};\n",
