        source: "import {\n  DxGrid,\n  DxTable,\n  DxTableHeaderRow,\n} from '@devexpress/dx-vue-grid-bootstrap4';\n\nimport {\n  generateRows,\n  globalSalesValues,\n} from '../../../demo-data/generator';\n\nconst MyHighlightedCell = {\n  inheritAttrs: false,\n  props: ['value'],\n  template: `\n    <dx-cell\n      :style=\"{ backgroundColor: value < 5000 ? 'red' : undefined }\"\n      v-bind=\"$attrs\"\n      v-on=\"$listeners\"\n    >\n      <span\n        :style=\"{ color: value < 5000 ? 'white' : undefined }\"\n      >\n        {{value}}\n      </span>\n    </dx-cell>\n  `,\n  components: {\n    DxCell: DxTable.components.DxCell,\n  },\n};\n\nconst MyCell = {\n  inheritAttrs: false,\n  props: ['column'],\n  data() {\n    return {\n      componentId: this.column.name === 'amount' ? 'my-highlighted-cell' : 'dx-cell',\n    };\n  },\n  template: `\n    <component\n      :is=\"componentId\"\n      :column=\"column\"\n      v-bind=\"$attrs\"\n      v-on=\"$listeners\"\n    />\n  `,\n  components: {\n    DxCell: DxTable.components.DxCell,\n    MyHighlightedCell,\n  },\n};\n\nexport default {\n  data() {\n    return {\n      columns: [\n        { name: 'region', title: 'Region' },\n        { name: 'sector', title: 'Sector' },\n        { name: 'channel', title: 'Channel' },\n        { name: 'customer', title: 'Customer' },\n        { name: 'product', title: 'Product' },\n        { name: 'amount', title: 'Sale Amount' },\n      ],\n      rows: generateRows({ columnValues: globalSalesValues, length: 8 }),\n    };\n  },\n  template: `\n    <div class=\"card\">\n      <dx-grid\n        :rows=\"rows\"\n        :columns=\"columns\"\n      >\n        <dx-table\n          :cellComponent=\"$options.components.MyCell\"\n        />\n        <dx-table-header-row />\n      </dx-grid>\n    </div>\n  `,\n  components: {\n    DxGrid,\n    DxTable,\n    DxTableHeaderRow,\n    MyCell,\n  },\n};\n",
