        source: "import {\n  DxGrid,\n  DxTable,\n  DxTableHeaderRow,\n} from '@devexpress/dx-vue-grid-bootstrap4';\n\nimport {\n  generateRows,\n  globalSalesValues,\n} from '../../../demo-data/generator';\n\nconst MyTable = {\n  inheritAttrs: false,\n  template: `\n    <dx-table\n      class=\"table-striped\"\n      v-bind=\"$attrs\"\n      v-on=\"$listeners\"\n    >\n      <slot />\n    </dx-table>\n  `,\n  components: {\n    DxTable: DxTable.components.DxTable,\n  },\n};\n\nexport default {\n  data() {\n    return {\n      columns: [\n        { name: 'region', title: 'Region' },\n        { name: 'sector', title: 'Sector' },\n        { name: 'channel', title: 'Channel' },\n        { name: 'customer', title: 'Customer' },\n        { name: 'product', title: 'Product' },\n        { name: 'amount', title: 'Sale Amount' },\n      ],\n      rows: generateRows({ columnValues: globalSalesValues, length: 8 }),\n    };\n  },\n  template: `\n    <div class=\"card\">\n      <dx-grid\n        :rows=\"rows\"\n        :columns=\"columns\"\n      >\n        <dx-table\n          :tableComponent=\"$options.components.MyTable\"\n        />\n        <dx-table-header-row />\n      </dx-grid>\n    </div>\n  `,\n  components: {\n    DxGrid,\n    DxTable,\n    DxTableHeaderRow,\n    MyTable,\n  },\n};\n",
