        demo: require('../src/demo-sources/grid-editing/none/disable-column-editing.js').default,
        source: "import { DxEditingState } from '@devexpress/dx-vue-grid';\nimport {\n  DxGrid,\n  DxTable,\n  DxTableHeaderRow,\n  DxTableEditRow,\n  DxTableEditColumn,\n} from '@devexpress/dx-vue-grid-bootstrap4';\nimport {\n  generateRows,\n  defaultColumnValues,\n} from '../../../demo-data/generator';\n\nexport default {\n  data() {\n    return {\n      columns: [\n        { name: 'name', title: 'Name' },\n        { name: 'sex', title: 'Sex' },\n        { name: 'city', title: 'City' },\n        { name: 'car', title: 'Car' },\n      ],\n      rows: generateRows({\n        columnValues: { id: ({ index }) => index, ...defaultColumnValues },\n        length: 8,\n      }),\n      editingStateColumnExtensions: [\n        { columnName: 'name', editingEnabled: false },\n      ],\n      editingRowIds: [0],\n      addedRows: [],\n      rowChanges: {},\n      deletedRowIds: [],\n    };\n  },\n  methods: {\n    getRowId(row) {\n      return row.id;\n    },\n    commitChanges({ added, changed, deleted }) {\n      let { rows } = this;\n      if (added) {\n        const startingAddedId = rows.length > 0 ? rows[rows.length - 1].id + 1 : 0;\n        rows = [\n          ...rows,\n          ...added.map((row, index) => ({\n            id: startingAddedId + index,\n            ...row,\n          })),\n        ];\n      }\n      if (changed) {\n        rows = rows.map(row => (changed[row.id] ? { ...row, ...changed[row.id] } : row));\n      }\n      if (deleted) {\n        const deletedSet = new Set(deleted);\n        rows = rows.filter(row => !deletedSet.has(row.id));\n      }\n      this.rows = rows;\n    },\n  },\n  template: `\n    <div class=\"card\">\n      <dx-grid\n        :rows=\"rows\"\n        :columns=\"columns\"\n        :getRowId=\"getRowId\"\n      >\n        <dx-editing-state\n          v-on:commitChanges=\"commitChanges\"\n          :columnExtensions=\"editingStateColumnExtensions\"\n          :addedRows.sync=\"addedRows\"\n          :rowChanges.sync=\"rowChanges\"\n          :deletedRowIds.sync=\"deletedRowIds\"\n          :editingRowIds.sync=\"editingRowIds\"\n        />\n        <dx-table />\n        <dx-table-header-row />\n        <dx-table-edit-row />\n        <dx-table-edit-column\n          showAddCommand\n          showEditCommand\n          showDeleteCommand\n        />\n      </dx-grid>\n    </div>\n  `,\n  components: {\n    DxGrid,\n    DxTable,\n    DxTableHeaderRow,\n    DxEditingState,\n    DxTableEditRow,\n    DxTableEditColumn,\n  },\n};\n",
