        source: "import {\n  DxGroupingState,\n  DxIntegratedGrouping,\n} from '@devexpress/dx-vue-grid';\nimport {\n  DxGrid,\n  DxTable,\n  DxTableHeaderRow,\n  DxTableGroupRow,\n} from '@devexpress/dx-vue-grid-bootstrap4';\n\nimport { generateRows } from '../../../demo-data/generator';\n\nexport default {\n  data() {\n    return {\n      columns: [\n        { name: 'name', title: 'Name' },\n        { name: 'sex', title: 'Sex' },\n        { name: 'city', title: 'City' },\n        { name: 'car', title: 'Car' },\n      ],\n      rows: generateRows({ length: 8 }),\n      grouping: [{ columnName: 'city' }],\n      expandedGroups: [],\n    };\n  },\n  template: `\n    <div class=\"card\">\n      <dx-grid\n        :rows=\"rows\"\n        :columns=\"columns\"\n      >\n        <dx-grouping-state\n          :grouping=\"grouping\"\n          :expandedGroups.sync=\"expandedGroups\"\n        />\n        <dx-integrated-grouping />\n        <dx-table />\n        <dx-table-header-row />\n        <dx-table-group-row />\n      </dx-grid>\n    </div>\n  `,\n  components: {\n    DxGroupingState,\n    DxIntegratedGrouping,\n    DxGrid,\n    DxTable,\n    DxTableHeaderRow,\n    DxTableGroupRow,\n  },\n};\n",
