        source: "import { DxSelectionState } from '@devexpress/dx-vue-grid';\nimport {\n  DxGrid,\n  DxTable,\n  DxTableHeaderRow,\n  DxTableSelection,\n} from '@devexpress/dx-vue-grid-bootstrap4';\n\nimport { generateRows } from '../../../demo-data/generator';\n\nexport default {\n  data() {\n    return {\n      columns: [\n        { name: 'name', title: 'Name' },\n        { name: 'sex', title: 'Sex' },\n        { name: 'city', title: 'City' },\n        { name: 'car', title: 'Car' },\n      ],\n      rows: generateRows({ length: 6 }),\n      selection: [1],\n    };\n  },\n  template: `\n    <div class=\"card\">\n      <dx-grid\n        :rows=\"rows\"\n        :columns=\"columns\"\n      >\n        <dx-selection-state\n          :selection.sync=\"selection\"\n        />\n        <dx-table />\n        <dx-table-header-row />\n        <dx-table-selection\n          selectByRowClick\n        />\n      </dx-grid>\n    </div>\n  `,\n  components: {\n    DxSelectionState,\n    DxGrid,\n    DxTable,\n    DxTableHeaderRow,\n    DxTableSelection,\n  },\n};\n",
